
@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+JP:wght@500&display=swap');
.App {
  font-family: 'Noto Serif JP', serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* PortfolioWebsite.css */

.portfolio-website {
  position: relative;
  border-radius: 1px solid #38B6FF;
}

.portfolio-website-title {
  font-size: 24px;
  margin-top: 0;
  margin-bottom: 10px;
}

.portfolio-website-description {
  margin-top: 0;
  margin-bottom: 20px;
}

.portfolio-website-image {
  width: 300px;
  max-width: 600px;
  height: 300px;
  margin-bottom: 20px;
}

.portfolio-website-links {
  margin-right: 1em;
  display: flex;
  justify-content: flex-end;
}

.portfolio-website-link {
  background-color: #333;
  color: #fff;
  padding: 10px 20px;
  margin-left: 10px;
  text-decoration: none;
  border-radius: 5px;
}

.portfolio-website-link:hover {
  background-color: #56b8f1;
  border: 1px solid white;
  cursor: pointer;
}

.portfolio-website::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: linear-gradient(15deg, transparent 50%, #38B6FF 30%);
  background-size: 300% 300%;
  transition: width 0.5s ease-in-out;
  z-index: -1;
}

.portfolio-website:hover::before {
  width: 100%;
}

.skillsContainerList {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
}

.skills-container p{
  margin-left:1em;
}
.skills-container h2{
  margin-left:.5em;
}
.work-container{
  width: 100%;
  transition: all 0.5s ease;
  background: linear-gradient(170deg, transparent 50%, #38B6FF 30%);
}

.work-container:is(:not(.active)) {
  transform: translateY(110%);
}

.work-container.active {
  transform: translateY(100);
}

.work-container h2{
  margin-left:.5em;
}

.skills{
  text-align: center;
  width: 8%;
  box-sizing: border-box;
  list-style: none;
  font-size: small;
  style: none;
  margin: 1em;
  border: 1px solid ccc;
  border-radius: 5px;
  padding:1em;
  padding-bottom: 2em;
  height: 1em;
  background-color: #38B6FF;
  background-image: none;
  transition: background 0.3s ease-in-out;
}
.skills:hover{
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  transition: background-color 0.3s ease-in-out;
  border: 1px solid #60c2fb;
  cursor:pointer;
  background: linear-gradient(15deg, transparent 50%, #38B6FF 30%);
}
.skillModal{
  margin: 1em;
}

.imgdiv{
  display: flex;
  justify-content: center;

}

.quote-form-overlay {
  position: fixed;
  top: 2%;
  left: 25%;
  width: 80%;
  height: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
  background-color: rgba(0, 0, 0, 0.8);

  flex-direction: column;
  align-items: center;
  padding: 40px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}
.quote-form-overlay button:hover{
  background-color: #38B6FF;
  cursor: pointer;
}

.quote-form-container {
  position: relative;
  width: 500px;
  max-width: 90%;
  padding: 50px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

input,
textarea,
button {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: none;
  border-radius: 4px;
  font-size: 16px;
}

input[type="submit"] {
  background-color: #60c2fb;
  color: #fff;
  cursor: pointer;
}

input[type="submit"]:hover {
  background-color: #3498db;
}

textarea {
  height: 100px;
}

@media screen and (min-width: 768px) {
  .quote-form-overlay {
    max-width: 600px;
  }
}

@media only screen and (max-width: 767px) {
  .App-logo {
  height: 20vmin;
  }
  
  .App-header {
  min-height: auto;
  }
  
  .portfolio-website-image {
  width: 100%;
  height: auto;
  }
  
  .skills {
  width: 25%;
  }
  
  .quote-form-overlay {
  left: 5%;
  width: 90%;
  }
  
  .quote-form-container {
  width: 100%;
  padding: 20px;
  }
  }
  
  
  @media only screen and (min-width: 1200px) {
  .portfolio-website {
  display: flex;
  justify-content: space-between;
  align-items: center;
  }
  
  .portfolio-website-description {
  margin-right: 50px;
  }
  
  .skillsContainer {
  justify-content: space-between;
  }
  }

